/*
`Phaser.Events.EventEmitter`  uma classe no Phaser que permite a criao e gerenciamento de eventos personalizados.  usada para implementar um sistema de eventos no Phaser, onde objetos podem emitir eventos e outros objetos podem se inscrever para ouvir esses eventos e responder a eles.

A classe `Phaser.Events.EventEmitter` fornece mtodos para emitir eventos, se inscrever em eventos e cancelar a inscrio de eventos. Alguns dos mtodos importantes incluem:

- `on(event: string, callback: Function, context?: any)`: Registra uma funo de retorno de chamada (callback) para um determinado evento. Sempre que o evento for emitido, a funo de retorno de chamada ser chamada.

- `emit(event: string, ...args: any[])`: Emite um evento, disparando todas as funes de retorno de chamada registradas para esse evento. Os argumentos adicionais passados para `emit` sero repassados para as funes de retorno de chamada.

- `off(event?: string, callback?: Function, context?: any, once?: boolean)`: Cancela a inscrio em um evento especfico. Se nenhum evento for fornecido, todas as inscries sero canceladas.  possvel filtrar a cancelao de inscrio com base na funo de retorno de chamada e no contexto.

O uso do `Phaser.Events.EventEmitter` permite que voc crie e gerencie facilmente eventos personalizados em seu jogo Phaser. Isso pode ser til para comunicao entre objetos, notificaes de eventos importantes, atualizaes de estado e muito mais.
*/
const sceneEvents = new Phaser.Events.EventEmitter();
export { sceneEvents };
